import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import Laopo1 from "./views/Laopo1.vue";
import Laopo2 from "./views/Laopo2.vue";
import Laopo3 from "./views/Laopo3.vue";
import Laopo4 from "./views/Laopo4.vue";
Vue.use(Router);

const router = new Router({
  mode: "history",

  routes: [
    {
      path: '/laopo1',
      name: "laopo1",
      component: Laopo1,
      props: true
    },
    {
      path: '/laopo2',
      name: "laopo2",
      component: Laopo2,
      props: true
    },
    {
      path: '/laopo3',
      name: "laopo3",
      component: Laopo3,
      props: true
    },
    {
      path: '/laopo4',
      name: "laopo4",
      component: Laopo4,
      props: true
    },
    {
      path: "/",
      name: "home",
      component: Home,
      props: true
    },
    {
      path: "/404",
      alias: "*",
      name: "notFound",
      component: () =>
        import("./views/NotFound")
    }
  ]
});


export default router;
